import { _decorator, Component, Vec3 } from "cc";
const { ccclass, property } = _decorator;

@ccclass("GravityHandler")
export class GravityHandler extends Component {
  onLoad() {
    super.onLoad();
  }

  async applyGravity(grid) {
    let moved = false;
    let promises: Promise<void>[] = [];

    do {
      moved = false;

      for (let col = 0; col < grid[0].length; col++) {
        for (let row = grid.length - 2; row >= 1; row--) {
          const currentPiece = grid[row][col];
          const belowPiece = grid[row - 1][col];

          // Eğer aşağıdaki hücre boşsa ve yukarıdaki hücre doluysa
          if (belowPiece.isEmpty && !currentPiece.isEmpty) {
            // Aşağıdaki boş hücreye yukarıdaki hücrenin node'unu taşı
            const targetRow = row + 1;
            const targetCol = col;

            // Yukarıdaki hücre node'unu aşağıdaki hücreye taşı
            promises.push(currentPiece.node.setPosition(new Vec3(targetCol, targetRow, 0)));

            // Node'u yer değiştir
            belowPiece.node = currentPiece.node;
            currentPiece.node = null; // Boş hücre olarak ayarla

            // Boş hücre ve dolu hücre bilgilerini güncelle
            belowPiece.isEmpty = false;
            currentPiece.isEmpty = true;

            // Hareket gerçekleştiğini belirle
            moved = true;
          }
        }
      }

      await Promise.all(promises);
    } while (moved); 
  }
}
